.friends {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.sidebar_eading {
    font-size: 25px;
    color: aliceblue;
}

.allFriends__block {
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: center;
    gap: 10px;
}

.friend {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.friend_name {
    color: white;
    font-size: 15px;
}

.userpic {
    max-width: 50px;
    max-height: 50px;
    border-radius: 50px;
}