.pages_numbers {
    max-width: 95%;
    display: flex;
    flex-wrap: wrap;
}

.selected_page {
    font-weight: bold;
    border: black solid 1px;
}

.unselected_page {
    cursor: pointer;
    border: brown dotted 1px;
}

.paginator {
    margin: 10px;
    display: flex;
    gap: 5px;
}