.users_wrapper {
    display: flex;
    justify-content: space-between;
    padding: 0 50px;
    gap: 20px;
}
.follow__block {
    max-width: 20%;
}

.userPhoto {
    max-width: 100px;
    max-height: 100px;
    border-radius: 20px;
}

.user_info__block {
    display: flex;
    justify-content: space-between;
    min-width: 60%;
    border: black solid 2px;
    border-radius: 20px;
    padding: 10px;
}

.status_block {
    display: flex;
    flex-direction: column;
}